<template>
  <div>
    <mytil>添加页</mytil>
    <div class="Box">
      <div class="inp_box">
        <input type="text" placeholder="书名" v-model='name'> <br>
        <input type="text" placeholder="描述" v-model='desc'> <br>
        <input type="text" placeholder="价格" v-model='price'><br>
        <button @click='add'>提交</button>
      </div>
    </div>
  </div>
</template>

<script>
import title from "../../common/title.vue";
export default {
  components: {
    mytil: title
  },
  data () {
      return {
          name:'',
          desc:'',
          price:0
      }
  },
  methods: {
      add(){
        //   把新数据放入vuex中的listData中，显示在列表页
        let obj = {
            bookName:this.name,
            bookInfo:this.desc,
            bookPrice:this.price,
            bookId:Math.random()
        };
        this.$store.commit('addList',obj)
        // 添加完成之后 跳转到列表页
        this.$router.push('/list')
      }
  }
};
</script>

<style lang="less" scoped>
.inp_box{
        margin: 1.5rem 0;
        text-align: center;
        input{
            border:1px solid #ccc;
            border-radius: 5px;
            height: 0.8rem;
            width: 80%;
            outline: none;
            font-size: 0.5rem;
        }
        button{
            font-size: 0.4rem;
        }
    }

</style>